export class CommentsPost
{
  constructor(data) {
    this.data = data
  }
  get shortComments() {
    return this.data.splice(0, 4)
  }
}

export class User 
{
  constructor(data) {
    this.data = data
  }
  get findName() {
    return {
      firstname: this.data.firstname,
      lastname: this.data.lastname,
      id: this.data.id,
      img: this.data.additionally.img,
      fone: this.data.additionally.fone
    }
  }
  get findLevel() {
    return {
      level: this.data.level,
      target: this.data.target,
      rank: this.data.rank,
      reputations: this.data.reputations
    }
  }
  get findAddition() {
    return {
      age: this.data.additionally.age,
      gender: this.data.additionally.gender,
      phone: this.data.additionally.phone,
      country: this.data.additionally.country,
      city: this.data.additionally.city,
      development: this.data.additionally.development,
      interesting: this.data.additionally.interesting,
      music: this.data.additionally.music,
      films: this.data.additionally.films,
      about: this.data.additionally.about,
      category: this.data.additionally.categories
    }
  }
  get blackList() {
    return this.data.blackList
  }
}

export class RankList {
  constructor() {
    this.data = [
      " ", " ", "", " I", 
      "", "", " ", 
      " ", " ", " II", 
      "", " ", " ", 
      " ", " ", " ", 
      " ", " ", " ", 
      " ", ""
    ]
    this.target = [ 0, 16, 32, 64, 128, 256, 512, 1024,
      2048, 5050, 12100, 20000, 30000, 50000, 75000,
      100000, 200000, 500000, 1000000, 5000000, 10000000 ]
    this.level = ""
  }
  set rankLevel(value) {
    this.level = this.data[value - 1]
  }
  get rankLevel() {
    return this.level
  }
  get targetFull() {
    return this.target
  }
  get ranksFull() {
    return this.data
  }
  get shortRanks() {
    return this.data.splice(0,4)
  }
}

export function capitalize(value) {
  return value.charAt(0).toUpperCase() + value.slice(1);
}
